/* Text meant only for screen readers */
/*
*   @usage: @include btn--icon(BEM-root-class);
*   Button with icon with white circular background.
*    --------------
*   |  BTN TEXT >  |
*    --------------
*   @param $rootBEM: root class name that will be extended
*/
/*
*   @usage: @include btn--icon--circle(BEM-root-class);
*   Button with icon with white circular background.
*    ---------------
*   |  BTN TEXT (>) |
*    ---------------
*   @param $rootBEM: root class name that will be extended
*/
/*
*   @usage: @include btn--icon--hover-circle(BEM-root-class);
*   Button with icon. Hovering/focusing button moves
*   the icon to right and fades in a white circular background
*    --------------                    ----------------
*   |  BTN TEXT >  | --hover/focus--> |  BTN TEXT  (>) |
*    --------------                    ----------------
*   @param $rootBEM: root class name that will be extended
*/
#enp-quiz .enp-screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
#enp-quiz .enp-screen-reader-text:hover,
#enp-quiz .enp-screen-reader-text:active,
#enp-quiz .enp-screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

