@keyframes removeAnswers {
  0% {
    opacity: 1;
    height: auto;
    transform: translate3d(0, 0, 0);
  }
  10% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
  100% {
    padding: 0;
    opacity: 0;
    display: none;
    height: 0;
    z-index: -1;
    transform: translate3d(-150px, 0, 0);
  }
}
@keyframes removeQuestion {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  15% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    transform: translateX(-150px);
  }
}
@keyframes showNextQuestion {
  0% {
    opacity: 0;
    transform: translateX(120px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slideInTop {
  0% {
    opacity: 0;
    height: auto;
    transform: translate3d(0, -20px, 0);
  }
  100% {
    opacity: 1;
    height: auto;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes slideInBottom {
  0% {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes flashRedShadow {
  0% {
    background: #bf5700;
  }
  100% {
    background: #fff;
  }
}
@keyframes errorContainer {
  100% {
    background-color: rgba(191, 87, 0, 0.2);
  }
}
@keyframes pop {
  0% {
    width: 0;
    opacity: 1;
  }
  30% {
    width: 100%;
  }
  80% {
    opacity: 1;
    width: 100%;
  }
  100% {
    width: 40%;
    opacity: 0;
  }
}
.spinner {
  margin: 0 auto;
  width: 70px;
  text-align: center;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #333;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}
@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
